i {
  cursor: pointer;
}

body.dark footer {
  background: #343a40;
}

 

#footer{
  background-color: #1D1D1D;
  text-align: center;
  padding:5% 10%;
  margin: 0 !important;
  display:flex;
  justify-content: space-around;
  
}

.last-one{
  margin-top:20px;
  margin-bottom:10px;
  color:#6aeded;
  
}

.social-icon{
  color:#60b2f6;
  margin:2% 5%;
  
  transition:0.4s;
  font-size:25px;
}




.social-icon:hover{
  color:rgb(0, 255, 89);
}